@import "normalizes.scss";
@import "resets.scss";
$color-violet: #724C9D;
$color-lavender: #9356A0;
$color-lilac: #DCCAE9;

@font-face {
    font-family: "MontserratT";
    src: url('../fonts/Montserrat-Regular.ttf');
    font-weight: regular;
    font-style: normal; }
@font-face {
    font-family: "MontserratT";
    src: url('../fonts/Montserrat-SemiBold.ttf');
    font-weight: bold;
    font-style: normal; }

.body {
    font-family: "MontserratT";
    font-weight: regular; }


* {
    box-sizing: border-box; }


.conteiner {
    max-width: 1140px;
