        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 64px;
        align-items: center; }
    &__logo {
        width: 134px; }
    &__contetn-container {
        position: relative;
        display: flex;
        flex-direction: row; }
    &__first-elipse {
        position: absolute;
        top: 85px;
        right: -43px;
        z-index: 0; }
    &__second-elipse {
        position: absolute;
        top: 650px;
        right: -120px; } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__item {
        margin-right: 64px;
        font-size: 18px;
        font-family: "MontserratT";
