a {
    text-decoration: none;
    color: black; }

.left-wrapper {
    position: relative;
    img {
        width: 863px;
        height: 700px;
        position: relative;
        z-index: 5; }
    &__rectangle-violet {
       position: absolute;
       left: -70px;
       top: 150px;
       z-index: {}
       width: 591.8px;
       height: 222.45px;
       background: $color-violet;
       border-radius: 100px;
       box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
       transform: rotate(-74.72deg);
       z-index: 2; }
    &__rectangle-lavender {
        position: absolute;
        width: 636.97px;
        height: 228.68px;
        left: -30px;
        top: 160px;
        background: $color-lavender;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-55.13deg);
        z-index: 3; }
    &__rectangle-lilac {
        position: absolute;
        width: 649.38px;
        height: 228.66px;
        left: 20px;
        top: 200px;
        background: $color-lilac;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-37.08deg);
