.icons {
    display: flex;
    flex-direction: row;
    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        justify-content: space-around;
        width: 40px;
        height: 40px;
        border: 2px solid $color-violet;
        border-radius: 100px;
        margin-right: 17px;
        box-shadow: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
        &_item:last-child {
            margin-right: 0p; }

        &__i {
            font-size: 18px;
            color: black; } } }
.title {
    font-size: 72px;
    margin-bottom: 5px;
    font-weight: bold;
    font-family: "MontserratT"; }


.buttons {
    display: flex;
    flex-direction: row;
    &__main-btn {
        height: 64px;
        width: 200px;
        background: $color-violet;
        color: #F9F9F9;
        font-size: 20px;
        text-align: center;
        border-radius: 100px;
        border: 1px solid $color-violet;
        margin-right: 32px; }
    &__icon-btn {
        margin-right: 12px; }
    &__text {
